  function loadPageData() {
    const params = {
      search,
      ...pageParams,
      ...tabs[filterIndex.tab].params,
      gameType: gameTypes[filterIndex.gameType].gameType,
      lineList: lines[filterIndex.line].line,
      providerList: providers[filterIndex.provider].provider
    }
    setPageData(getPageData(gameList, params))
    setLoaded(true)
  }

  function setFilterParams(provider) {
    for (let i = providers.length; i--;) {
      if (providers[i].provider === provider) {
        return setFilterIndex(state => ({
          ...state,
          tab: 1,
          provider: i
        }))
      }
    }
  }

  function bindScrollEvent() {
    function handelScroll() {
      const rect = gameListEl.current.getBoundingClientRect()
      if (rect.bottom <= document.body.clientHeight && pageYOffset < window.pageYOffset) {
        loadNextPage()
      }
      pageYOffset = window.pageYOffset
    }

    window.addEventListener('scroll', handelScroll)
    return () => window.removeEventListener('scroll', handelScroll)
  }

  function loadNextPage() {
    if (!loaded) return
    if (pageParams.page < pageData.maxPage) {
      setLoaded(false)
      const timer = setTimeout(() => {
        setPageParams((state) => ({
          ...state,
          page: state.page + 1
        }))
        clearTimeout(timer)
      }, 300)
    }
  }

  function selectedFilterIndex(newState) {
    if (newState.tab && newState.tab === tabs.length - 1 && !loginStatus) {
      return dialog('请登录后查看')
    }
    setPageParams(state => ({
      ...state,
      page: 1
    }))
    setFilterIndex((state) => ({
      ...state,
      ...newState
    }))
  }

  function getWmsImgPath() {
    const wmsServer = window.OMConfig.wmsServer
    const wmsCdnPath = window.OMConfig.wmsCdnPath
    const wmsEnvPath = window.OMConfig.wmsEnvPath
    return `${wmsServer + wmsCdnPath}/_wms/img/${wmsEnvPath}/electronicgames/`
  }

  function setSearchValue(e) {
    if (e.type === 'keypress' && e.which !== 13) return
    setSearch(inputEl.current.value)
  }

  function getGameStatus(code) {
    return gameStatus[code] && +gameStatus[code].flag
  }

