/**
 * @description 获取游戏列表
 * @param {String} url
 * @return {Promise}
 * */
export function getGames(url) {
  const head = document.getElementsByTagName('head')[0]
  const script = document.createElement('script')
  return new Promise((resolve, reject) => {
    script.src = url
    script.type = 'text/javascript'
    script.onload = script.onreadystatechange = function () {
      if (!this.readyState || this.readyState === 'loaded' || this.readyState === 'complete') {
        const games = window.games || []
        window.games = null
        resolve(changeToObject(games))
      } else {
        reject(new Error('获取游戏列表失败'))
      }
      head.removeChild(script)
    }
    head.appendChild(script)
  })
}

/**
 * @description 将游戏数据二维数组转换为一维数组，把值与字段按照下标关联起来
 * @param  {Array} list gamesJs文件中的二维数组
 * @return {Array}      组装为对象后的游戏数据数组
 */
function changeToObject(list = []) {
  let obj
  list = list.map(item => {
    obj = {
      isFavorite: false, // 是否收藏
      gameCode: platformCode[item[1]] // 游戏平台code
    }
    item.forEach((game, index) => {
      obj[keys[index]] = game
    })
    return obj
  })
  return sort(list, 'popularity')
}

/**
 * @description 数组指定字段、方式排序
 * @param  {Array}  list
 * @param  {String} sortField 排序字段
 * @param  {String} sortType  排序方式 asc 升序, desc 降序
 * @return {Array} list
 */
function sort(list, sortField, sortType = 'desc') {
  list.sort((a, b) => {
    a = sortField ? +a[sortField] : a
    b = sortField ? +b[sortField] : b
    return sortType === 'desc' ? b - a : a - b
  })
  return list
}

/**
 * @description 将数组组装为分页数据
 *
 * @param  {Array}  gameList 游戏数组
 * @param  {Object} params   过滤参数
 * @return {Object}          分页数据
 */
export function getPageData(gameList = [], params) {
  gameList = filterList(gameList, params)
  const { page, pageSize } = params
  const total = gameList.length
  const data = gameList.slice(0, page * pageSize)
  const maxPage = Math.ceil(gameList.length / pageSize)
  return {
    page,
    pageSize,
    data,
    total,
    maxPage
  }
}

/**
 * @description 将数组按照参数过滤后返回
 *
 * @param  {Array}  gameList 游戏数组
 * @param  {Object} params   过滤参数
 * @return {Array}
 */
function filterList(gameList, params) {
  const { search, gameType, providerList, lineList } = params
  if (!gameList.length) return gameList
  // 搜索游戏名称
  if (search) {
    const reg = new RegExp(search)
    gameList = gameList.filter(item => {
      return reg.test(item.cnName) || reg.test(item.enName)
    })
  }
  // 筛选游戏大类（热门、最新、彩金池...）
  for (const key in params) {
    if (params.hasOwnProperty(key) && params[key] === true) {
      gameList = gameList.filter(item => item[key])
    }
  }
  // 筛选游戏类型
  if (gameType) {
    gameList = gameList.filter(item => item.gameType === gameType)
  }
  // 筛选游戏平台
  if (providerList) {
    if (isArray(providerList) && providerList.length) {
      gameList = gameList.filter(item => ~providerList.indexOf(item.provider))
    } else if (typeof providerList === 'string') {
      gameList = gameList.filter(item => providerList === item.provider)
    }
  }
  // 筛选游戏线路
  if (lineList) {
    if (isArray(lineList) && lineList.length) {
      const list = []
      lineList.forEach(line => {
        list.push(...filterLine(line))
      })
      gameList = list
    } else if (typeof lineList === 'string') {
      gameList = filterLine(lineList)
    }
  }

  function filterLine(line) {
    line = line.split('-')
    !line[1] && line.push(line[0])
    return gameList.filter(game => {
      return game.line >= line[0] && game.line <= line[1]
    })
  }

  return gameList
}

/**
 * @description 进入游戏初始方法
 *
 * @param {Object} param    参数对象
 *                 gameId   游戏id
 *                 gameCode 游戏平台code
 *                 gameType 游戏类型
 *                 gameName 游戏名称
 *                 tryPlay  是否支持试玩，此参数在判断是否支持试玩时具有优先级
 */
export function playGame(param = {}) {
  const { gameCode, canTryPlay } = param
  const loginStatus = store.getState().customer.loginName
  param.canTryPlay = canTryPlay || tryPlayCode[gameCode] || tryPlayCode[platformCode[gameCode]]
  if (loginStatus) {
    getGameUrl(param)
  } else if (param.canTryPlay) {
    getGameUrl(param)
  } else {
    // toast.error({
    //   duration: 1500,
    //   message: '请先登录',
    //   callback() {
    //     router.push('/login').then(r => r)
    //   }
    // })
  }
}

/**
 * @description 获取游戏路径
 *
 * @param {Object}  param      参数对象
 */
function getGameUrl(param) {
  const routeData = router.resolve({
    path: '/loadingGame',
    query: param
  })
  window.open(routeData.href, '_blank', 'height=667, width=375, scrollbars=yes,resizable=1,modal=false,alwaysRaised=yes')
}

export default {
  getGames,
  getPageData
}
